<template>
  <div>
    <mynav></mynav>
  </div>
</template>

<script>
  import Mynav from '../components/Header.vue'

  export default {
    name: 'Home',
    components: {
      Mynav
    }
  }
</script>

<style scoped>
  .header-state {
    background: #fff;
    height: 48px;
    font-size: 16px;
    line-height: 48px;
    box-shadow: 0 3px 3px rgba(0, 0, 0, 0.12);
    font-weight: 400;
  }

  .header-state>div {
    width: 756px;
    margin: 0 auto;
  }

  .header-state span {
    cursor: pointer;
    display: inline-block;
    height: 45px;
    text-align: center;
    width: 150px;
    color: #777;
  }

  .header-state span.active {
    color: #468df1;
    border-bottom: 3px solid #468df1;
  }

  .describe {
    margin: 24px auto;
    color: #999;
    width: 768px;
    font-size: 18px;
    font-weight: 600;
    /*font-family: "宋体";*/
  }

  .describe p {
    padding-left: 6px;
    line-height: 1;
  }

  .projects, .projects-public {
    margin: 24px auto;
    width: 768px;
    flex-wrap: wrap;
    /*justify-content: space-around;*/
  }

  .card {
    background: #fff;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.2), 0 1px 5px 0 rgba(0,0,0,0.12);
    line-height: 32px;
    width: 244px;
    height: 254px;
    margin: 8px 6px;
  }

  .card:hover {
    box-shadow: 0 2px 8px 0 rgba(0,0,0,.14), 0 3px 4px -5px rgba(0,0,0,.2), 0 1px 8px 0 rgba(0,0,0,.12);
  }

  .card .color {
    height: 136px;
    background-size: cover;
    background-position: center;
    background-color: #468df1;
    text-align: center;
  }

  .color>i {
    line-height: 136px;
    color: #fff;
    font-size: 52px;
  }

  .card .content {
    margin-top: 12px;
    font-size: 14px;
    padding-left: 12px;
    color: rgba(0, 0, 0, 0.8);
  }

  .card .content .name {
    font-size: 16px;
    font-weight: 600;
    color: #222;
  }

  .join-btn {
    color: #468df1;
  }

</style>


